@import url(https://fonts.googleapis.com/css2?family=Oranienbaum&family=Prompt:wght@300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.brew{display:flex;justify-content:center;align-items:center}.brew button{font-size:2.75rem;text-transform:uppercase}.brew .text{display:inline-flex;justify-content:space-between;align-items:baseline;width:9.5rem;margin-bottom:1rem}.brew .text label{border-bottom:solid 2px transparent}.brew .count{display:inline-block;margin-left:.15ch;font-size:5rem;color:#5F5E5E;position:relative}.brew .count.number-1{margin-right:.25ch;position:relative;top:2px}.selected .text label{border-color:rgba(255,119,37,0.52)}

.controls{display:flex;justify-content:space-between;align-items:center;padding:0 .25rem}.controls button{-webkit-appearance:none;-moz-appearance:none;appearance:none;display:flex;justify-content:center;align-items:center;width:5.5rem;height:5rem;position:relative;top:-.5rem;border:none;border-radius:100%;background-color:#fff;color:#5F5E5E;font-family:'Oranienbaum', serif;font-size:1.6rem}.controls button span{color:#c7c4c4}.controls button span{display:inline-block;margin-top:2rem;pointer-events:none}.pwa .controls button{top:1rem}.pwa .controls span{margin-top:0;pointer-events:none}

.color{display:grid;grid-template-rows:repeat(5, 19%);grid-template-columns:auto;align-content:center;color:#c7c4c4}.color .active{color:#5F5E5E;transition:color 100ms ease-out}.color .oo{letter-spacing:-.777rem;margin-right:.74rem}.color .text{display:inline-block;font-family:'Prompt', serif;text-transform:uppercase;font-size:1.8rem}

.input--temperature{width:100%;height:75%;position:absolute}.temperature{display:grid;grid-template-areas:"a" "value" "label";grid-template-columns:1fr;grid-template-rows:1fr 1fr 1fr;position:relative}.temperature .value{grid-area:value;display:flex;justify-content:center;align-items:center;line-height:1.1}.temperature .value .amount{font-size:4.5rem;position:relative;left:2px;bottom:5px;pointer-events:none}.temperature .label{grid-area:label;display:flex;justify-content:center;align-items:center}.temperature .label .amount-label:first-child{width:50%;margin-right:.25rem}.temperature .label .amount-label:last-child{width:50%;margin-left:0.25rem}.circle{grid-area:value;display:flex;justify-content:center;align-items:flex-end;min-width:9.25rem;min-height:6rem;border:2px solid #A0A0A0;border-top:none;border-radius:100%;margin-bottom:0.5rem}.selected .circle{border-color:rgba(255,119,37,0.52)}

.timer{width:150px;margin:0 auto;display:flex;flex-direction:column;justify-content:center}.input--timer{width:100%;height:100%;position:absolute}.capsule{display:flex;justify-content:space-between;align-items:center;margin:0 auto;width:9rem;padding:0;padding-left:.5rem;padding-right:1rem;border:2px solid #A0A0A0;border-radius:500px;position:relative}.capsule .label{font-size:1.1rem}.values{display:inline-flex;justify-content:space-between;align-items:center;width:4.8rem}.values .value{line-height:1.05}.value{display:inline-block;min-width:1ch;height:4.5rem;font-size:4.5rem;text-align:center;line-height:1}.sec{position:relative;top:-2px}.selected-min .min.capsule{border-color:rgba(255,119,37,0.52)}.selected-min .sec.capsule{border-top-color:transparent}.selected-sec .sec.capsule{border-color:rgba(255,119,37,0.52)}.selected-sec .min.capsule{border-bottom-color:transparent}

.name{max-height:100px}.name.start .name__name{color:#c7c4c4;border-bottom:1px solid #c7c4c4}.name .name__name{font-size:2.5rem;border-bottom:1px solid #5F5E5E;min-height:42px}.name .input--text{min-height:42px}.selected .name__name{border-color:rgba(255,119,37,0.52) !important}

@keyframes done{0%{opacity:0}100%{opacity:1}}@keyframes out{0%{opacity:1}100%{opacity:0}}.overlay{position:absolute;top:0;left:0;width:100%;height:100%;position:absolute;background:linear-gradient(180deg, rgba(245,164,115,0.9), transparent);opacity:0;z-index:20;pointer-events:none}.overlay.done{animation:done 1.0s forwards;pointer-events:auto}.overlay.out{animation:out 300ms}

.weight{display:flex;justify-content:center;align-items:center;position:relative}.input--weight{width:100%;height:75%;margin-top:-15%;position:absolute}.diamond{grid-area:diamond;display:flex;justify-content:center;align-items:center;width:6rem;height:6rem;border:2px solid #A0A0A0;transform-origin:center;transform:rotate(45deg);position:absolute;pointer-events:none}.diamond .value{font-size:4.5rem;transform:rotate(-45deg)}.diamond .tsp,.diamond .g{position:absolute;pointer-events:all}.diamond .tsp{transform:rotate(-45deg);top:6.6rem}.diamond .g{transform:translate(4.2rem, .4rem) rotate(-45deg)}.selected .diamond{border-color:rgba(255,119,37,0.52)}

.input--water{width:100%;height:75%;position:absolute}.water{display:grid;grid-template-areas:"a" "value" "label";grid-template-columns:1fr;grid-template-rows:1fr 1fr 1fr;position:relative}.water .value{grid-area:value;display:flex;justify-content:center;align-items:center;line-height:1.1}.water .value .amount{font-size:4.5rem}.water .label{grid-area:label;display:flex;justify-content:center;align-items:center}.water .label .amount-label:first-child{width:50%;margin-right:.25rem}.water .label .amount-label:last-child{width:50%;margin-left:0.25rem}.glass{grid-area:value;display:flex;justify-content:center;align-items:flex-end;min-width:8rem;min-height:5.2rem;border:2px solid #A0A0A0;border-top:none;margin-bottom:0.3rem}.selected .glass{border-color:rgba(255,119,37,0.52)}

.history{width:100%;height:100vh;display:grid;grid-template-areas:'notch' 'controls' 'gap' 'sessions';grid-template-columns:100%;grid-template-rows:2% 2% 2% 92%}.gap{grid-area:gap}.sessions{grid-area:sessions;padding:0 1rem}

.tea-session{display:flex;flex-direction:column;width:100%}.tea-session .light{color:grey}.tea-session .mystery{color:grey;font-size:1.4rem}.tea-session .header{display:flex;justify-content:space-between;align-items:center;margin-top:3ch;margin-bottom:1.5ch;padding-top:1ch;border-bottom:1px solid #f7f7f7}.tea-session .header .name{font-size:1.75rem;margin-right:.5ch}.tea-session .header .id{color:grey;font-size:1.1rem}.tea-session .labels,.tea-session .brew-session{display:grid;grid-template-columns:repeat(auto-fit, minmax(20px, 1fr));grid-template-rows:1fr;justify-items:center}.tea-session .labels .unit,.tea-session .brew-session .unit{font-size:.8rem;color:grey;margin-left:2px}.tea-session .labels{font-size:.9rem;font-style:italic;color:grey;margin-bottom:.5ch}.tea-session .brew-session{margin-bottom:1ch}

html,body,div,span,applet,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,abbr,acronym,address,big,cite,code,del,dfn,em,img,ins,kbd,q,s,samp,small,strike,strong,sub,sup,tt,var,b,u,i,center,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,canvas,details,embed,figure,figcaption,footer,header,hgroup,menu,nav,output,ruby,section,summary,time,mark,audio,video{margin:0;padding:0;border:0;font-size:100%;font:inherit;vertical-align:baseline}article,aside,details,figcaption,figure,footer,header,hgroup,menu,nav,section{display:block}body{line-height:1}ol,ul{list-style:none}blockquote,q{quotes:none}blockquote:before,blockquote:after,q:before,q:after{content:'';content:none}table{border-collapse:collapse;border-spacing:0}

html{width:100%;height:100%;font-family:'Oranienbaum', serif;font-size:16px}@media screen and (min-width: 425px){html{background-color:#fcfffd}}body{background-color:#fff;color:#5F5E5E}@media screen and (min-width: 425px){body{height:100vh;width:425px;margin:0 auto}}main{background-color:#fff;position:relative}@media screen and (min-width: 425px){main{border:1px solid #f7f7f7}}.main{display:grid;grid-template-areas:'notch notch' 'controls controls' 'name name' 'color weight' 'gap1 gap1' 'temp water' 'gap2 gap2' 'timer brew' 'gap3 gap3';grid-template-columns:1fr 1fr;grid-template-rows:2% 2% 14% 22% 0 22% 2% 22% 5%;overflow:hidden}@media screen and (min-width: 425px){.main{grid-template-rows:4vh 2vh 17vh 20vh 2vh 26vh 0 28vh 2vh}}.main.pwa{grid-template-rows:4vh 2vh 17vh 20vh 2vh 26vh 0 28vh 2vh}input,button{-webkit-appearance:none;-moz-appearance:none;appearance:none;border:none;background:none;font-family:inherit;color:inherit}input:focus,button:focus{outline:none}input{opacity:0;position:absolute}.name{grid-area:name}.controls{grid-area:controls}.name{grid-area:name}.temperature{grid-area:temp}.color{grid-area:color}.weight,.water,.timer,.temperature,.color,.brew{transform-origin:center;transform:scale(0.75);min-width:100%;min-height:100%;align-self:center}.pwa .weight,.pwa .water,.pwa .timer,.pwa .temperature,.pwa .color,.pwa .brew{transform:scale(0.85)}.weight{grid-area:weight}.water{grid-area:water}.timer{grid-area:timer}.brew{grid-area:brew}.f-cc{display:flex;justify-content:center;align-items:center}.amount-label{font-size:1.8rem;color:#c7c4c4}.amount-label.active{color:#5F5E5E}.sip{position:fixed;bottom:0;text-align:center;left:0;right:0;color:#e4ffe4}

